<template>
  <div @click.prevent="randomizeEffect" />
</template>

<script>
import * as THREE from "three";
import * as shaders from "@/vendor/shaders";
import { EffectComposer } from "@/vendor/postprocessing/EffectComposer";
import { RenderPass } from "@/vendor/postprocessing/RenderPass";
import { ShaderPass } from "@/vendor/postprocessing/ShaderPass";

export default {
  name: 'CyberSpace',
  data() {
    return {
      width: 0,
      height: 0,
      scene: new THREE.Scene(),
      camera: new THREE.PerspectiveCamera(),
      renderer: new THREE.WebGLRenderer(),
      grid: null,
      cube: null,
      pyramid: null,
      solar: null,
      AFID: null,
      shaderTime: 0,
      composer: null,
      renderPass: null,
      copyPass: new ShaderPass(shaders.copy),
      effectPasses: {
        tv: new ShaderPass(shaders.tv),
        rgb: new ShaderPass(shaders.rgb),
        film: new ShaderPass(shaders.film)
      },
      effectParams: {
        tv: {
          distortion: 0.1,
          distortion2: 0.1,
          speed: 0,
          rollSpeed: 0,
        },
        rgb: {
          angle: 0,
          amount: 0
        },
        film: {
          grayscale: 0,
          sCount: 400,
          sIntensity: 0.8,
          nIntensity: 0.4,
        },
      },
    };
  },
  computed: {
    aspect() {
      return this.width / this.height;
    },
  },
  watch: {
    width() {
      this.renderer.setSize(this.width, this.height);
    },
    height() {
      this.renderer.setSize(this.width, this.height);
    },
    aspect() {
      this.camera.aspect = this.aspect;
      this.camera.updateProjectionMatrix();
    },
  },
  mounted() {
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.antialias = true;
    this.renderer.shadowMap.enabled = true;
    this.initializeTest();
    this.$nextTick(() => window.addEventListener('resize', this.resize));
    this.randomizeEffect();
    this.resize();
  },
  methods: {
    initializeTest() {
      this.camera.near = 0.1;
      this.camera.far = 5000;
      this.camera.position.z = 100;
      this.camera.position.y = 10;

      var cubeGeometry = new THREE.BoxGeometry( 1000, 1000, 1000 );
      var material = new THREE.MeshBasicMaterial( { color: 0x33cccc } );
      material.wireframe = true;

      var pyramidGeometry = new THREE.CylinderGeometry(0, 500, 500, 3, false);
      var pyramidMaterial = new THREE.MeshDepthMaterial({ color: 0x000000 });
      this.pyramid = new THREE.Mesh( pyramidGeometry, pyramidMaterial );
      this.pyramid.position.z = -2000;
      //this.pyramid.rotation.z = 90 * (180 / Math.PI);
      this.scene.add(this.pyramid);

      var solarGeometry = new THREE.SphereGeometry(250, 64, 64);
      var solarMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff });
      this.solar = new THREE.Mesh( solarGeometry, solarMaterial);
      this.solar.position.z = -3000;
      this.solar.position.y = 350;
      this.scene.add(this.solar);

      this.cube = new THREE.Mesh( cubeGeometry, material );
      this.scene.add(this.cube);

      this.grid = new THREE.GridHelper( 200000, 10000, 0x9900ff, 0xff0088 );
      this.scene.add(this.grid);

      this.scene.fog = new THREE.FogExp2( 0x33cccc, 0.0025 );
      this.renderer.setClearColor( 0x2b2b2b, 1 );
      this.composeShaders();
    },
    resize() {
      this.dequeue();
      this.$el.innerHTML = "";
      this.$nextTick(() => {
        this.width = this.$el.clientWidth;
        this.height = this.$el.clientHeight;
        this.$el.appendChild(this.renderer.domElement);
        this.queueNextFrame();
      });
      //
    },
    dequeue() {
      cancelAnimationFrame(this.AFID);
    },
    queueNextFrame() {
      this.AFID = requestAnimationFrame(this.animate);
    },
    animate() {
      this.nextFrame();
      this.queueNextFrame();
      if (Math.random() < 0.01) {
        this.randomizeEffect();
      }
    },
    nextFrame() {
      this.cube.rotation.x += 0.01;
      this.cube.rotation.y += 0.01;
      this.camera.position.z -= 0.1;

      this.shaderTime += 0.1;
      Object.keys(this.effectPasses).map(e => {
        if (this.effectPasses[e].uniforms['time']) {
          this.effectPasses[e].uniforms['time'].value = this.shaderTime;
        }
      })

      this.render();
    },
    render() {
      this.renderer.render(this.scene, this.camera);
      this.composer.render( 1);
    },
    composeShaders() {
      this.composer = new EffectComposer(this.renderer);
      this.renderPass = new RenderPass(this.scene, this.camera);
      this.composer.addPass(this.renderPass);
      const effects = Object.keys(this.effectPasses);
      effects.map(e => {
        this.composer.addPass(this.effectPasses[e]);
      });
      this.composer.addPass(this.copyPass);
      this.copyPass.renderToScreen = true;
    },
    randomizeEffect() {
      if (Math.random() < 0.1){
				this.effectParams.tv.distortion = 3;
				this.effectParams.tv.distortion2 = 1;
				this.effectParams.tv.speed = 0.3;
				this.effectParams.tv.rollSpeed = 0;
				this.effectParams.rgb.angle = 2;
				this.effectParams.rgb.amount = 0.05;

			} else {
				this.effectParams.tv.distortion = Math.random()*10+0.1;
				this.effectParams.tv.distortion2 =Math.random()*10+0.1;
				this.effectParams.tv.speed =Math.random()*0.4;
				this.effectParams.tv.rollSpeed =Math.random()*0.1;
				this.effectParams.rgb.angle = Math.random()*2;
				this.effectParams.rgb.amount = Math.random()*0.03;
        // this.effectParams.film.sCount = 800;
        // this.effectParams.film.sIntensity: 0.8;
        // this.effectParams.film.nIntensity: 0.4;
			}
			this.effectParamsChanged();
    },
    effectParamsChanged() {
      const effects = Object.keys(this.effectPasses);
      effects.map(e => {
        const params = Object.keys(this.effectParams[e]);
        params.map(p => {
          this.effectPasses[e].uniforms[p].value = this.effectParams[e][p];
        });
      });
    }
  },
}
</script>



