<template>
  <form
    class="search"
    @submit.prevent="search">
    <input
      ref="search"
      v-model="query"
      placeholder="//search"
      type="search"
      autocomplete="off"
      autocorrect="off"
      autocapitalize="off"
      spellcheck="false"
      expanded>
  </form>
</template>

<script>
const searchEngine = 'https://duckduckgo.com/?q=';

export default {
  data() {
    return {
      query: '',
    };
  },
  computed: {
    isUri() {
      if (/^([^\s]+)\.([^\s]+)$/.test(this.query)) {
        return true;
      }
      const a = document.createElement('a');
      a.href = this.query;
      return (a.host && a.host !== window.location.host);
    },
  },
  mounted() {
    this.$refs.search.focus();
  },
  methods: {
    search() {
      let query = this.query;
      if (this.isUri) {
        if (query.slice(0, 2) !== '//') {
          query = `//${query}`;
        }
        window.location.href = query;
      } else {
        window.location.href = `${searchEngine}${query}`;
      }
    },
  },
};
</script>



